module.exports = "/* Inspirated from  http://www.alessioatzeni.com/wp-content/tutorials/html-css/CSS3-loading-animation-loop/index.html */\nbody {\n    overflow: hidden;\n}\n.circle {\n    position: fixed;\n    margin: auto;\n    overflow: show;\n    top: 0;\n    left: 0;\n    bottom: 0;\n    right: 0;\n    display: flex;\n    justify-content: center;\n    align-items: center;\n    background-color: darkslategray;\n    z-index: 5;\n    opacity: 0.5;\n}\n.ouro {\n    position: relative;\n    display:inline-block;\n    height: 46px;\n    width: 46px;\n    margin: 1em;\n    border-radius: 50%;  \n    background: none repeat scroll 0 0 #DDDDDD;\n    overflow:hidden;\n    box-shadow: 0 0 10px rgba(0,0,0,.1) inset, 0 0 25px rgba(0,0,255,0.075);\n}\n.ouro:after {\n    content: \"\";\n    position: absolute;\n    top: 9px; left: 9px;\n    display: block;\n    height: 28px; width: 28px;\n    background: none repeat scroll 0 0 #F2F2F2;\n    border-radius: 50%;\n    box-shadow: 0 0 10px rgba(0,0,0,.1);\n}\n.ouro > span {\n    position: absolute;\n    height: 100%; width: 50%;\n    overflow: hidden;\n}\n.left  { left:0   }\n.right { left:50% }\n.anim {\n    position: absolute;\n    left: 100%; top: 0;\n    height: 100%; width: 100%;\n    border-radius: 999px;\n    background: none repeat scroll 0 0 #508EC3;\n    opacity: 0.8;\n    -webkit-animation: ui-spinner-rotate-left 3s infinite;\n    animation: ui-spinner-rotate-left 3s infinite;\n    -webkit-transform-origin: 0 50% 0;\n    transform-origin: 0 50% 0;\n}\n.left .anim {\n    border-bottom-left-radius: 0;\n    border-top-left-radius: 0;\n}\n.right .anim {\n    border-bottom-right-radius: 0;\n    border-top-right-radius: 0;\n    left: -100%;\n    -webkit-transform-origin: 100% 50% 0;\n    transform-origin: 100% 50% 0;\n}\n/* v2 */\n.ouro2 .anim {\n   -webkit-animation-delay:0;\n   animation-delay:0;\n}\n.ouro2 .right .anim{\n   -webkit-animation-delay: 1.5s;\n   animation-delay: 1.5s;\n}\n/* v3 */\n.ouro3 .anim {\n   -webkit-animation-delay: 0s;\n   -webkit-animation-duration:3s;\n   -webkit-animation-timing-function: linear;\n   animation-delay: 0s;\n   animation-duration:3s;\n   animation-timing-function: linear;\n}\n.ouro3 .right .anim{\n   -webkit-animation-name: ui-spinner-rotate-right;\n   -webkit-animation-delay:0;\n   -webkit-animation-delay: 1.5s;\n   animation-name: ui-spinner-rotate-right;\n   animation-delay:0;\n   animation-delay: 1.5s;\n}\n/* round variation */\n.round .ouro:after {display:none }\n/* double variation */\n.double .ouro:after {\n  height: 13px; width: 13px;\n  left: 7px; top: 7px;\n  border: 10px solid #ddd;\n  background: transparent;\n  box-shadow: none;\n}\n@keyframes ui-spinner-rotate-right{\n  0%{-webkit-transform:rotate(0deg);transform:rotate(0deg)}\n  25%{-webkit-transform:rotate(180deg);transform:rotate(180deg)}\n  50%{-webkit-transform:rotate(180deg);transform:rotate(180deg)}\n  75%{-webkit-transform:rotate(360deg);transform:rotate(360deg)}\n  100%{-webkit-transform:rotate(360deg);transform:rotate(360deg)}\n}\n@keyframes ui-spinner-rotate-left{\n  0%{-webkit-transform:rotate(0deg);transform:rotate(0deg)}\n  25%{-webkit-transform:rotate(0deg);transform:rotate(0deg)}\n  50%{-webkit-transform:rotate(180deg);transform:rotate(180deg)}\n  75%{-webkit-transform:rotate(180deg);transform:rotate(180deg)}\n  100%{-webkit-transform:rotate(360deg);transform:rotate(360deg)}\n}\n@-webkit-keyframes ui-spinner-rotate-right{\n  0%{-webkit-transform:rotate(0deg)}\n  25%{-webkit-transform:rotate(180deg)}\n  50%{-webkit-transform:rotate(180deg)}\n  75%{-webkit-transform:rotate(360deg)}\n  100%{-webkit-transform:rotate(360deg)}\n}\n@-webkit-keyframes ui-spinner-rotate-left{\n  0%{-webkit-transform:rotate(0deg)}\n  25%{-webkit-transform:rotate(0deg)}\n  50%{-webkit-transform:rotate(180deg)}\n  75%{-webkit-transform:rotate(180deg)}\n  100%{-webkit-transform:rotate(360deg)}\n}\n/*\n * Some bugs with Chrome (Android), Safari and Opera, I'll try to see how http://atomeye.com/projects/sass-css-spinner.html made his code.\n */\n/* presentation styles */\nhtml {height: 100%}\nbody { text-align:center; background: radial-gradient(circle, #fff 0%, #bbb 85%) no-repeat; background: -webkit-radial-gradient(circle, #fff 0%, #bbb 85%) no-repeat; height: 100%; display:table; width:100%}\n.block {display: table-cell; vertical-align:middle}\nh1, a { margin-top: 1em; font-family: \"Open Sans Light\", \"Open Sans\", \"Segoe UI\", Helvetica, Arial; color: #888; font-weight: lighter;}\n.info {margin-top: 25px}\n.info a {font-size: 12px; color: #999}\n.info br + a {text-decoration:none}\n/*# sourceMappingURL=data:application/json;base64, */"